// import logo from './logo.svg';
import './App.css';
import Projet from './components/Projet';
import OldProjet from './components/OldProjet';
import PiedPage from './components/Footer';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import DatasArr from './components/DatasArr';

function App() {
  return (
    <div className="App">
    <Container>
        <Row>
          <Col md={4}>
            
            <Projet ElementObj = {DatasArr[0]}/>
          
          </Col>
          <Col md={{ span: 4, offset: 4 }}>
            <OldProjet/>
            
            </Col>
        </Row>
        <Row>
          <Col md={{ span: 3, offset: 3 }}>
            
            <OldProjet/>
          
          </Col>
          <Col md={{ span: 3, offset: 3 }}><OldProjet/></Col>
        </Row>
        <Row>
          <Col md={{ span: 6, offset: 3 }}><PiedPage/>
          </Col>
        </Row>
    </Container>
     {/* <ThemeSwitcher/> */}

    </div>
  );
}

export default App;
