import Card from 'react-bootstrap/Card';
import React from "react";


function Projet(props) {
  return (
  <Card className="bg-dark text-black">
    <Card.Img src={props.ElementObj.miniature} alt="Test imgSimon" />
    <Card.ImgOverlay>
      <Card.Title>{props.ElementObj.titre}</Card.Title>
    </Card.ImgOverlay>
    
  </Card>
  );
}

export default Projet;
