import Card from 'react-bootstrap/Card';
import Autoportrait from '../images/miniature-autoportrait.png';
import React from "react";

function Projet () {
  return (
  <Card className="bg-dark text-black">
    <Card.Img src={Autoportrait} alt="Test imgSimon" />
    <Card.ImgOverlay>
      <Card.Title>Titre projet 2</Card.Title>
      <Card.Text>paragraphe projet 2</Card.Text>
    </Card.ImgOverlay>
  </Card>
  );
}

export default Projet;
